import { useEffect, useCallback, useMemo } from "react";
import useLocalStorage from "./useLocalStorage";
import getData from "../services/backend/getData";
import postData from "../services/backend/postData";

export default function useArticleAuthors() {
  const [articleAuthors, setLocalStorageArticleAuthors] = useLocalStorage(
    "articleAuthors",
    []
  );

  useEffect(() => {
    async function fetchData() {
      const data = await getData();
      if (data && data.articleAuthors) {
        setLocalStorageArticleAuthors(data.articleAuthors);
      }
    }
    fetchData();
  });

  const addArticleAuthor = useCallback(
    (articleAuthor) => {
      const _articleAuthors = [...articleAuthors, articleAuthor];
      setLocalStorageArticleAuthors(_articleAuthors);
      postData({ articleAuthors: _articleAuthors });
    },
    [articleAuthors]
  );

  const removeArticleAuthor = useCallback(
    (articleAuthorId) => {
      const _articleAuthors = [...articleAuthors];
      _articleAuthors[articleAuthorId]["deleted"] = true;
      setLocalStorageArticleAuthors(_articleAuthors);
      postData({ articleAuthors: _articleAuthors });
    },
    [articleAuthors]
  );

  const updateArticleAuthor = useCallback(
    (articleAuthor) => {
      const _articleAuthors = [...articleAuthors];
      _articleAuthors[articleAuthor.id] = articleAuthor;
      setLocalStorageArticleAuthors(_articleAuthors);
      postData({ articleAuthors: _articleAuthors });
    },
    [articleAuthors]
  );

  const getArticleAuthorById = useCallback(
    (id) => {
      return articleAuthors[id];
    },
    [articleAuthors]
  );

  const exists = (a) => a && typeof a === "object" && !a.deleted;

  const { articlesByAuthor, authorsByArticle } = useMemo(() => {
    return articleAuthors.reduce(
      (a, { articlesByAuthor, authorsByArticle }) => {
        if (!exists(a)) {
          return { articlesByAuthor, authorsByArticle };
        }
        const { authorID, articleId } = a;
        if (!articlesByAuthor[authorID]) {
          articlesByAuthor[authorID] = [];
        }
        articlesByAuthor[authorID].push(a);
        if (!authorsByArticle[articleId]) {
          authorsByArticle[articleId] = [];
        }
        authorsByArticle[articleId].push(a);
        return { authorsByArticle, articleAuthors };
      },
      { articlesByAuthor: {}, authorsByArticle: {} }
    );
  }, [articleAuthors]);

  return [
    articleAuthors.filter(exists),
    articlesByAuthor,
    authorsByArticle,
    addArticleAuthor,
    removeArticleAuthor,
    updateArticleAuthor,
    articleAuthors.length,
    getArticleAuthorById,
  ];
}
